.read-more {
  justify-content: center;
  align-items: center;
  display: flex;
}

.read-more__button {
  fill: #d41443;
  background-color: #fff;
  border-radius: 50%;
  padding: 11px;
  transition: scale .25s cubic-bezier(.4, 0, .2, 1);
}

.read-more__button:hover, .read-more__button:focus {
  transform: scale(130%);
}

.extra-text {
  font-family: var(--primary-font-family);
  text-align: center;
  color: #fff;
  margin-bottom: 32px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.3125;
  display: none;
}

#read-more-1:checked ~ .extra-text {
  display: block;
}

.less {
  display: none;
}

#read-more-1:checked ~ .read-more {
  transform: rotate(-90deg)scale(130%);
}

#read-more-1:checked ~ .read-more .more {
  display: none;
}

#read-more-1:checked ~ .read-more .less {
  display: inline;
}

